  methods:{
    drawChart() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("seven_map"));
      // 指定图表的配置项和数据
      let option = {
        legend: {},
        tooltip: {
            trigger: 'axis',
            showContent: false
        },
        dataset: {
            source: [
                ['time', '05:00', '08:00', '11:00','14:00','18:00'],
                ['装机', 41.1, 30.4, 65.1, 53.3, 83.8, 98.7],
