            ]
        },
        xAxis: {type: 'category'},
        yAxis: {gridIndex: 0},
        series: [
            {type: 'bar', smooth: true, seriesLayoutBy: 'row'},
            {type: 'bar', smooth: true, seriesLayoutBy: 'row'},
            {type: 'bar', smooth: true, seriesLayoutBy: 'row'},
            {type: 'line', smooth: true, seriesLayoutBy: 'row'},
        ]
      };
      myChart.setOption(option);
    }
  },
  mounted(){
    this.drawChart();
  }
}
