<script>
import Vue from 'vue'
import { Button, Tooltip ,Icon} from 'element-ui'
Vue.use(Button)
Vue.use(Tooltip)
Vue.use(Icon)
export default {
  name: 'module_four',
  props: {
  },
  data(){
    return{}
  },
  methods:{
    drawChart() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("fdl_map"));
      // 指定图表的配置项和数据
      let option = {
        legend: {},
        tooltip: {
          trigger: 'axis',
          showContent: false
        },
        dataset: {
          source: [
            ['time', '05:00', '08:00', '11:00','14:00','18:00'],
            ['实际发电量', 41.1, 30.4, 65.1, 53.3, 83.8, 98.7],
            ['风速', 24.1, 67.2, 79.5, 86.4, 65.2, 82.5],
          ]
        },
        xAxis: {type: 'category'},
        yAxis: {gridIndex: 0},
        series: [
          {type: 'bar', smooth: true, seriesLayoutBy: 'row'},
          {type: 'line', smooth: true, seriesLayoutBy: 'row'},
        ]
      };
      myChart.setOption(option);
    }
  },
  mounted(){
    this.drawChart();
  }
}
</script>

